<template>
  <div class="result-container">
    <div class="title-wrap">
      <!-- 顶部标题 -->
      <h2 class="title">{{ $route.query.q }}</h2>
      <span class="sub-title">找到{{ count }}个结果</span>
    </div>
    <el-tabs v-model="activeIndex">
      <el-tab-pane label="歌曲" name="songs">
        <table class="el-table">
          <thead>
            <th></th>
            <th>音乐标题</th>
            <th>歌手</th>
            <th>专辑</th>
            <th>时长</th>
          </thead>
          <tbody>
            <tr
              class="el-table__row"
              v-for="(item, index) in songList"
              :key="index"
              @dblclick="playMusic(item.id)"
              style="cursor: pointer;"
            >
              <td>{{ index + 1 }}</td>
              <td>
                <div class="song-wrap">
                  <div class="name-wrap">
                    <span>{{ item.name }}</span>
                    <span
                      class="iconfont icon-mv"
                      v-if="item.mvid != 0"
                      @click="playMv(item.mvid)"
                    ></span>
                  </div>
                  <!-- 二级歌名 -->
                  <span v-if="item.alias.length != 0">{{ item.alias[0] }}</span>
                </div>
              </td>
              <td>{{ item.artists[0].name }}</td>
              <td>{{ item.album.name }}</td>
              <td>{{ item.duration | date }}</td>
            </tr>
          </tbody>
        </table>
      </el-tab-pane>
      <el-tab-pane label="歌单" name="lists">
        <div class="items">
          <div
            class="item"
            v-for="(item, index) in playlists"
            :key="index"
            @click="toPlaylist(item.id)"
          >
            <div class="img-wrap">
              <div class="num-wrap">
                播放量:
                <span class="num">{{ item.playCount | playCount }}</span>
              </div>
              <img :src="item.coverImgUrl" alt="" />
              <span class="iconfont icon-play"></span>
            </div>
            <p class="name">{{ item.name }}</p>
          </div>
        </div>
      </el-tab-pane>
      <el-tab-pane label="MV" name="mv">
        <div class="items mv">
          <div
            class="item"
            v-for="(item, index) in mv"
            :key="index"
            @click="toMv(item.id)"
          >
            <div class="img-wrap">
              <img :src="item.cover" alt="" />
              <span class="iconfont icon-play"></span>
              <div class="num-wrap">
                <div class="iconfont icon-play"></div>
                <div class="num">{{ item.playCount | playCount }}</div>
              </div>
              <span class="time">{{ item.duration | date }}</span>
            </div>
            <div class="info-wrap">
              <div class="name">{{ item.name }}</div>
              <div class="singer">{{ item.artistName }}</div>
            </div>
          </div>
        </div>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "result",
  data() {
    return {
      // tab切换时改变数据
      activeIndex: "songs",
      // 保存查询歌曲数据
      songList: [],
      // 歌单
      playlists: [],
      //mv
      mv: [],
      // 搜索结果的总数
      count: 0,
    };
  },
  watch: {
    activeIndex() {
      let type = 1;
      // 获取个数
      let limit = 10;
      switch (this.activeIndex) {
        case "songs":
          type = 1;
          limit = 10;
          break;
        case "lists":
          type = 1000;
          limit = 10;
          break;
        case "mv":
          type = 1004;
          limit = 8;
          break;
        default:
          break;
      }
      axios({
        url: "/api/search",
        method: "get",
        params: {
          keywords: this.$route.query.q,
          type,
          limit,
        },
      }).then((res) => {
        // 歌曲
        if (type == 1) {
          this.songList = res.data.result.songs;
          this.count = res.data.result.songCount;
        } else if (type == 1000) {
          // 歌单
          this.playlists = res.data.result.playlists;
          // 总数
          this.count = res.data.result.playlistCount;
        } else {
          //mv
          this.mv = res.data.result.mvs;
          this.count = res.data.result.mvCount;
          console.log(res);
        }
      });
    },
  },
  created() {
    // 搜索页面
    axios({
      url: "/api/search",
      method: "get",
      params: {
        keywords: this.$route.query.q,
        type: 1,
      },
    }).then((res) => {
      this.songList = res.data.result.songs;
      this.count = res.data.result.songCount;
    });
  },
  methods: {
    // 播放mv
    playMv(id) {
      this.$router.push(`\mv?q=${id}`);
    },
    toMv(id) {
      this.$router.push(`\mv?q=${id}`);
    },
    // 去歌单详情页
    toPlaylist(id) {
      // 跳转并携带数据
      this.$router.push(`/playlist?q=${id}`);
    },
    // 播放音乐
    playMusic(id) {
      axios({
        url: "/api/song/url",
        method: "get",
        params: { id },
      }).then((res) => {
        let url = res.data.data[0].url;
        // 传值给index组件 设置给父组件的播放地址
        this.$parent.musicUrl = url;
      });
    },
  },
  filters: {
    // 分钟
    date(time) {
      let duration = time;
      let min = parseInt(duration / 1000 / 60);
      if (min < 10) {
        min = "0" + min;
      }
      let sec = parseInt((duration / 1000) % 60);
      if (sec < 10) {
        sec = "0" + sec;
      }
      return (duration = min + ":" + sec);
    },
    // 次数
    playCount(count) {
      let play =
        parseInt(count) > 10000
          ? parseInt(count / 10000) + "万"
          : parseInt(count);
      return play;
    },
  },
};
</script>

<style></style>
