    area() {
      this.page = 1;
      this.getList();
    },
    type() {
      this.page = 1;
      this.getList();
    },
    order() {
      this.page = 1;
      this.getList();
    },
  },
  created() {
  //  this.getList();
  },
  methods: {
    playMv(id) {
      this.$router.push(`\mv?q=${id}`);
    },
    //  获取列表数据
    getList() {
      axios({
        url: "/api/mv/all",
        method: "get",
        params: {
          area: this.area,
          type: this.type,
          order: this.order,
          // 取出数量 , 默认为 30
          limit: this.limit,
          //  偏移数量 , 用于分页 , 如 :( 页数 -1)*50, 其中 50 为 limit 的值 , 默认 为 0
          offset: this.page - 1,
        },
      }).then((res) => {
        this.list = res.data.data;
        // 有count就赋值
        if (res.data.count) {
          this.total = res.data.count;
        }
      });
    },
    // 页码改变
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
      this.page = val;
      this.getList();
    },
  },
  filters: {
    playCount(count) {
      let play =
        parseInt(count) > 10000
          ? parseInt(count / 10000) + "万"
          : parseInt(count);
      return play;
    },
  },
};
