<template>
  <div class="mv-container">
    <div class="mv-wrap">
      <h3 class="title">mv详情</h3>
      <!-- mv -->
      <div class="video-wrap">
        <video controls :src="url"></video>
      </div>
      <!-- mv信息 -->
      <div class="info-wrap">
        <div class="singer-info">
          <div class="avatar-wrap">
            <img :src="singerPic" alt="" />
          </div>
          <span class="name">{{ mvDetail.artistName }}</span>
        </div>
        <div class="mv-info">
          <h2 class="title">{{ mvDetail.name }}</h2>
          <span class="date">发布：{{ mvDetail.publishTime }}</span>
          <span class="number"
            >播放：{{ mvDetail.playCount | playCount }}次</span
          >
          <p class="desc">{{ mvDetail.desc }}</p>
        </div>
      </div>
      <!-- 精彩评论 -->
      <div class="comment-wrap">
        <p class="title">
          精彩评论<span class="number">({{ hotComments.length }})</span>
        </p>
        <div class="comments-wrap">
          <div class="item" v-for="(item, index) in hotComments" :key="index">
            <div class="icon-wrap">
              <img :src="item.user.avatarUrl" alt="" />
            </div>
            <div class="content-wrap">
              <div class="content">
                <span class="name">{{ item.user.nickname }}：</span>
                <span class="comment">{{ item.content }}</span>
              </div>
              <div class="re-content" v-if="item.beReplied.length != 0">
                <span class="name"
                  >{{ item.beReplied[0].user.nickname }}：</span
                >
                <span class="comment">{{ item.beReplied[0].content }}</span>
              </div>
              <div class="date">{{ item.time | dateFormatMinute }}</div>
            </div>
          </div>
        </div>
      </div>
      <!-- 最新评论 -->
      <div class="comment-wrap">
        <p class="title">
          最新评论<span class="number">({{ count }})</span>
        </p>
        <div class="comments-wrap">
          <div class="item" v-for="(item, index) in comments" :key="index">
            <div class="icon-wrap">
              <img :src="item.user.avatarUrl" alt="" />
            </div>
            <div class="content-wrap">
              <div class="content">
                <span class="name">{{ item.user.nickname }}：</span>
                <span class="comment">{{ item.content }}</span>
              </div>
              <div class="re-content" v-if="item.beReplied.length != 0">
                <span class="name"
                  >{{ item.beReplied[0].user.nickname }}：</span
                >
                <span class="comment">{{ item.beReplied[0].content }}</span>
              </div>
              <div class="date">{{ item.time | dateFormatMinute }}</div>
            </div>
          </div>
        </div>
      </div>
      <!-- 分页器 -->
      <el-pagination
        @current-change="handleCurrentChange"
        background
        layout="prev, pager, next"
        :total="total"
        :current-page="page"
        :page-size="5"
        :limit="limit"
      >
      </el-pagination>
    </div>
    <div class="mv-recommend">
      <h3 class="title">相关推荐</h3>
      <div class="mvs">
        <div class="items">
          <div class="item" v-for="(item, index) in simiMvs" :key="index">
            <div class="img-wrap">
              <img :src="item.cover" alt="" />
              <span class="iconfont icon-play"></span>
              <div class="num-wrap">
                <div class="iconfont icon-play"></div>
                <div class="num">{{ item.playCount | playCount }}</div>
              </div>
              <span class="time">{{ item.duration | date }}</span>
            </div>
            <div class="info-wrap">
              <div class="name">{{ item.name }}</div>
              <div class="singer">{{ item.artistName }}</div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "mv",
  data() {
    return {
      // 总条数
      total: 20,
      // 页码
      page: 1,
      // 页容量
      limit: 10,
      // mv的地址
      url: "",
      // 相关mv
      simiMvs: [],
      // mv信息
      mvDetail: {},
      // 歌手头像
      singerPic: "",
      // 最新评论
      comments: "",
      // 最新评论数量
      count: 0,
      // 热门评论
      hotComments: "",
    };
  },
  created() {
    // mv地址
    axios({
      url: "/api/mv/url",
      method: "get",
      params: {
        id: this.$route.query.q,
      },
    }).then((res) => {
      this.url = res.data.data.url;
    });
    // 相关mv
    axios({
      url: "/api/simi/mv",
      method: "get",
      params: {
        mvid: this.$route.query.q,
      },
    }).then((res) => {
      this.simiMvs = res.data.mvs;
      // console.log(this.simiMvs);
    });
    // mv信息
    axios({
      url: "/api/mv/detail",
      method: "get",
      params: {
        mvid: this.$route.query.q,
      },
    }).then((res) => {
      this.mvDetail = res.data.data;
      // console.log(res.data.data);
      // 歌手信息
      axios({
        url: "/api/artists",
        method: "get",
        params: {
          id: this.mvDetail.artists[0].id,
        },
      }).then((res) => {
        // console.log(res.data);
        this.singerPic = res.data.artist.picUrl;
      });
    });
    // 获取评论的数据
    axios({
      url: "/api/comment/mv",
      method: "get",
      params: {
        id: this.$route.query.q,
        limit: this.limit,
        offset: (this.page - 1) * this.limit,
      },
    }).then((res) => {
      // console.log(res);
      this.hotComments = res.data.hotComments;
      // this.hotCount = this.hotComments.length;
      this.comments = res.data.comments;
      this.count = this.comments.length;
    });
  },
  methods: {
    // 获取评论
    getComments() {
      // 获取评论的数据
      axios({
        url: "/api/comment/mv",
        method: "get",
        params: {
          id: this.$route.query.q,
          limit: this.limit,
          offset: (this.page - 1) * this.limit,
        },
      }).then((res) => {
        // console.log(res);
        this.comments = res.data.comments;
        // this.hotComments = res.data.hotComments;
      });
    },
    handleCurrentChange(val) {
      // console.log(`当前页: ${val}`);
      this.page = val;
      this.getComments();
    },
  },
  filters: {
    // 时长
    date(time) {
      let duration = time;
      let min = parseInt(duration / 1000 / 60);
      if (min < 10) {
        min = "0" + min;
      }
      let sec = parseInt((duration / 1000) % 60);
      if (sec < 10) {
        sec = "0" + sec;
      }
      return (duration = min + ":" + sec);
    },
    // 时间过滤
    dateFormat(originVal) {
      const dt = new Date(originVal);
      const year = dt.getFullYear();
      const month = (dt.getMonth() + 1 + "").padStart(2, "0");
      const date = (dt.getDate() + "").padStart(2, "0");
      return `${year}-${month}-${date}`;
    },
    // 格式 2020:10:30
    dateFormatMinute(originVal) {
      const dt = new Date(originVal);
      const year = dt.getFullYear();
      const month = (dt.getMonth() + 1 + "").padStart(2, "0");
      const date = (dt.getDate() + "").padStart(2, "0");
      const hour = (dt.getHours() + "").padStart(2, "0");
      const minute = (dt.getMinutes() + "").padStart(2, "0");
      const seconds = (dt.getSeconds() + "").padStart(2, "0");
      return `${year}-${month}-${date} ${hour}:${minute}:${seconds}`;
    },
    playCount(count) {
      let play =
        parseInt(count) > 10000
          ? parseInt(count / 10000) + "万"
          : parseInt(count);
      return play;
    },
  },
};
</script>

<style></style>
