<template>
  <div class="songs-container">
    <div class="tab-bar">
      <span class="item" :class="{ active: tag == 0 }" @click="tag = '0'"
        >全部</span
      >
      <span class="item" :class="{ active: tag == 7 }" @click="tag = '7'"
        >华语</span
      >
      <span class="item" :class="{ active: tag == 96 }" @click="tag = '96'"
        >欧美</span
      >
      <span class="item" :class="{ active: tag == 8 }" @click="tag = '8'"
        >日本</span
      >
      <span class="item" :class="{ active: tag == 16 }" @click="tag = '16'"
        >韩国</span
      >
    </div>
    <!-- 底部的table -->
    <table class="el-table playlit-table">
      <thead>
        <th></th>
        <th></th>
        <th>音乐标题</th>
        <th>歌手</th>
        <th>专辑</th>
        <th>时长</th>
      </thead>
      <tbody>
        <tr class="el-table__row" v-for="(item, index) in lists" :key="index">
          <td>{{ index + 1 }}</td>
          <td>
            <div class="img-wrap">
              <img :src="item.album.picUrl" alt="" />
              <!-- 播放按钮 -->
              <span
                class="iconfont icon-play"
                @click="playMusic(item.id)"
              ></span>
            </div>
          </td>
          <td>
            <div class="song-wrap">
              <div class="name-wrap">
                <span>{{ item.name }}</span>
                <span
                  class="iconfont icon-mv"
                  v-if="item.mvid != 0"
                  @click="playMv(item.mvid)"
                ></span>
              </div>
            </div>
          </td>
          <td>{{ item.album.artists["0"].name }}</td>
          <td>{{ item.album.name }}</td>
          <td>{{ item.duration | date }}</td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

