  methods: {
    // 播放mv
    playMv(id) {
      this.$router.push(`\mv?q=${id}`);
    },
    // 获取列表数据
    getList() {
      axios({
        url: "/api/top/song",
        method: "get",
        params: {
          type: this.tag,
        },
      }).then((res) => {
        this.lists = res.data.data;
        // 处理时长
        // for (let i = 0; i < this.lists.length; i++) {
