        //   this.lists[i].duration = `${min}:${sec}`;
        // }
        console.log(res.data.data);
      });
    },
    // 播放音乐
    playMusic(id) {
      axios({
        url: "/api/song/url",
        method: "get",
        params: {
          id,
        },
      }).then((res) => {
        let url = res.data.data[0].url;
        this.$parent.musicUrl = url;
        // console.log(url);
      });
    },
  },
  filters: {
    date(time) {
      let duration = time;
      let min = parseInt(duration / 1000 / 60);
      if (min < 10) {
        min = "0" + min;
      }
      let sec = parseInt((duration / 1000) % 60);
      if (sec < 10) {
        sec = "0" + sec;
      }
      return (duration = `${min}:${sec}`);
    },
  },
